*{
  margin: 0;
  padding: 0;
  list-style: none;
} 

body{
  /* background: red; */
  box-sizing: border-box;
  display: flex;
  flex-direction: row-reverse;
  margin-right: 25%;
}

button{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin: 10px;
  border-radius: 5px;
  background: lightblue;
  border: 5px;
  padding: 10px 20px;
  font-weight: 700;
}
.App{
  display: flex;
}

img{
  border-radius: 20px;
  margin-left: 5px;
  margin-top: 50px;
}

 

.container {
  padding: 16px;
}

/* Full-width input fields */
input[type=text], input[type=password] {
  width: 100%;
  padding: 15px;
  margin: 5px 0 22px 0;
  display: inline-block;
  border: none;
  background: #f1f1f1;
}

input[type=text]:focus, input[type=password]:focus {
  background-color: #ddd;
  outline: none;
}

/* Overwrite default styles of hr */
hr {
  border: 1px solid #f1f1f1;
  margin-bottom: 25px;
}

/* Set a style for the submit/register button */
.registerbtn {
  background-color: #04AA6D;
  color: white;
  padding: 16px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
  opacity: 0.9;
  justify-content: center;
}

.registerbtn:hover {
  opacity:1;
}

/* Add a blue text color to links */
a {
  color: dodgerblue;
}

/* Set a grey background color and center the text of the "sign in" section */
.signin {
  background-color: #f1f1f1;
  text-align: center;
}
